import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { AucItem } from '../item';
import { Bid } from '../bid';


@Injectable({ providedIn: 'root' })
export class RpcService {

    private itemsUrl = 'api/items';

    httpOptions = {
        headers: new HttpHeaders({ 'Content-Type': 'application/json' })
    };

    constructor(private http: HttpClient) { }

    getItems(params): Observable<AucItem[]> {

        const ps = new HttpParams()
            .set('page', params.page ? params.page : 1)
            .set('sort', params.sort ? params.sort : 'creation_dt')
            .set('order', params.order ? params.order : 'desc')
            .set('search_string', params.search_string ? params.search_string : null);

        return this.http.get<AucItem[]>(this.itemsUrl, { params: ps })
            .pipe(
                catchError(this.handleError<AucItem[]>('getItems', []))
            );
    }

    getBids(item_id: Number): Observable<Bid[]> {

        const url = `api/items/${item_id}/bids`

        return this.http.get<Bid[]>(this.itemsUrl)
            .pipe(
                catchError(this.handleError<Bid[]>('getBids', []))
            );
    }


    getItem(item_id: Number): Observable<AucItem> {
        let items: AucItem[] = [
            {id: 1, title: 'Harry Potter', description: 'Book of magic', price: 16.60},
            {id: 2, title: 'Lord of the rings', description: 'The Fellowship of the Ring', price: 20.31},
            {id: 3, title: 'Clean code', description: 'A Handbook of Agile Software Craftsmanship', price: 34.99}
        ];

        let item: AucItem;

        for (let it of items) {
            if (it.id == item_id) {
                item = it;
                break;
            }
        }
        return of(item);
    }


    addItem(item: AucItem): Observable<{}> {
        const url = 'api/items';
        return this.http.post(url, item)
            .pipe(
                catchError(this.handleError('addItem'))
            );
    }

    makeBid(item_id: Number, bid_price: Number): Observable<{}> {
        const url = `api/items/{item_id}/bids`;
        return this.http.post(url, {price: bid_price})
            .pipe(
                catchError(this.handleError('makeBid'))
            );
    }

    updateItem(item: AucItem): Observable<{}> {
        const url = 'api/items/' + item.id.toString(10);
        return this.http.put(url, item, this.httpOptions)
            .pipe(
                catchError(this.handleError('updateItem'))
            );
    }

    deleteItem(item_id: number): Observable<{}> {
        const url = `api/items/${item_id}`;
        return this.http.delete(url, this.httpOptions)
            .pipe(
                catchError(this.handleError('deleteItem'))
            );
    }

    /**
     * Handle Http operation that failed.
     * Let the app continue.
     * @param operation - name of the operation that failed
     * @param result - optional value to return as the observable result
     */
    private handleError<T>(operation = 'operation', result?: T) {
        return (error: any): Observable<T> => {
            console.error(error); // log to console

            // Let the app keep running by returning an empty result.
            return of(result as T);
        };
    }
}
