import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { AucItem } from '../item';
import { RpcService } from '../rpc/rpc.service';

@Component({
  selector: 'items-list',
  templateUrl: './items-list.component.html',
  styles: [],
  encapsulation: ViewEncapsulation.None
})
export class ItemsListComponent implements OnInit {
    items: AucItem[];
    selectedItem: AucItem;
    mode: String = 'list_view';
    page: Number = 1;

    constructor(private rpcService: RpcService) { }

    ngOnInit() {
        this.getItems();
    }

    getItems(): void {
        this.rpcService.getItems({page: this.page})
            .subscribe(items => this.items = items);
    }

    onSelect(item: AucItem): void {
        this.selectedItem = item;
        this.mode = 'item_view';
    }

    onSelectEdit(item: AucItem): void {
        this.selectedItem = item;
        this.mode = 'item_edit';
    }

    onSelectDelete(item_id: number): void {
        if (!confirm('Are you sure want to delete this item?')) {
            return;
        }

        this.rpcService.deleteItem(item_id)
            .subscribe(() => this.getItems());
    }

    onAddNewItem(): void {
        this.selectedItem = {
            title: null,
            description: null,
            price: null,
            close_dt: null
        };
        this.mode = 'add_new_item';
    }

    setListViewMode(refresh_list: Boolean): void {
        this.mode = 'list_view';
        if (refresh_list) {
            this.getItems();
        }
    }

    onItemCardEvent(ev: String) {
        if (ev == 'new_item_created') {
            this.setListViewMode(true);
        } else if (ev == 'item_card_closed') {
            this.setListViewMode(false);
        } else if (ev == 'item_update') {
            this.setListViewMode(true);
        }
    }
}
